import React, { useState } from "react";
import PropTypes from "prop-types";
import {
  Group,
  CardGrid,
  CellButton,
  View,
  ModalRoot,
  ModalPage,
  Cell,
  Switch,
  ModalPageHeader,
  Title,
} from "@vkontakte/vkui";

import Icon24Sort from "@vkontakte/icons/dist/24/sort";

import ProjectCard from "./ProjectCard";

const MAIN_MODAL = "main-modal";

const ProjectCardList = (props) => {
  const { events } = props;

  const [currentModal, setCurrentModal] = useState(null);

  const filter = (
    <ModalRoot
      activeModal={currentModal}
      onClose={() => {
        setCurrentModal(null);
      }}
    >
      <ModalPage
        id={MAIN_MODAL}
        header={<ModalPageHeader><Title level="2">–§–∏–ª—å—Ç—Ä—ã</Title></ModalPageHeader>}
        dynamicContentHeight
      >
        <Cell asideContent={<Switch />}>–†—è–¥–æ–º</Cell>
        <Cell asideContent={<Switch />}>–°–∫–æ—Ä–æ</Cell>
        <Cell asideContent={<Switch />}>–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏ –∫ –∑–∞–ø–∏—Å—è–º</Cell>
        <Cell></Cell><Cell></Cell>
      </ModalPage>
    </ModalRoot>
  );

  return (
    <View modal={filter}>
      <Group
        separator="hide"
        header={
          <CellButton
            before={<Icon24Sort />}
            onClick={() => {
              setCurrentModal(MAIN_MODAL);
            }}
          >
            –§–∏–ª—å—Ç—Ä–æ–≤–∞—Ç—å
          </CellButton>
        }
      >
        <CardGrid>
          {events.map((project, key) => (
            <ProjectCard key={key} project={project} />
          ))}
        </CardGrid>
      </Group>
    </View>
  );
};

ProjectCardList.propTypes = {
  events: PropTypes.array.isRequired,
};

export default ProjectCardList;
